html{background-color:#3400cd}html header{width:100%;display:flex;justify-content:space-around}html header h1{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:30px;margin:10px;color:#ffdc93}html header ul{display:flex;justify-content:space-between;list-style:none;z-index:100;margin:10px}html header ul a{color:white}html header ul a:visited{color:lightgray}html header ul a img{width:40px;height:40px;margin:5px}html header ul a img:hover{margin:3px;width:44px;height:44px}html footer{color:#ffdc93;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:20px auto}body{width:100vw;height:100vh}body section.game{position:relative;width:1000px;margin:0 auto;min-height:600px;max-width:100vw}body section.game canvas{position:absolute;max-width:100vw;border:2px solid white;box-sizing:border-box}
